
import { Header } from '@/components/app/header';
import { Hero } from '@/components/app/hero';
import { ApplicationSteps } from '@/components/app/application-steps';
import { Testimonial } from '@/components/app/testimonial';
import { Footer } from '@/components/app/footer';
import { CoreValues } from '@/components/app/core-values';
import { UpcomingEvents } from '@/components/app/upcoming-events';
import eventsData from '@/lib/events.json';
import { PlaceHolderImages } from '@/lib/placeholder-images';
import type { Event } from '@/lib/events';
import { parseEventDate } from '@/lib/date-utils';

export default function Home() {
  const now = new Date();
  
  const upcomingEvents = eventsData.events
    .map(event => ({
      ...event,
      parsedDate: parseEventDate(event.meetupTime || event.date)
    }))
    .filter(event => event.parsedDate && event.parsedDate > now)
    .sort((a, b) => a.parsedDate!.getTime() - b.parsedDate!.getTime()) // Oldest first
    .slice(0, 3)
    .map(event => {
      const image = PlaceHolderImages.find(img => img.id === event.imageId);
      return { ...event, image };
    });

  return (
    <div className="flex flex-col min-h-screen bg-background">
      <Header />
      <main className="flex-grow">
        <Hero />
        <CoreValues />
        <ApplicationSteps />
        <Testimonial />
        <UpcomingEvents events={upcomingEvents} />
      </main>
      <Footer />
    </div>
  );
}
